import React from "react";
import { createRef, useRef } from "react";
// reactstrap components
import { Button, Container } from "reactstrap";
import image from '../../assets/img/Slide01.jpg'
// core components

function LandingPageHeader(props) {
  let pageHeader = React.createRef();
  const {contactUsRef}=props
  React.useEffect(() => {
    if (window.innerWidth < 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });



  const executeScroll = () => contactUsRef.current.scrollIntoView({ behavior: "smooth" });  
  return (
    <>
      <div
        style={{
        
         backgroundImage: `url(${image})`,
        }}
        className="page-header"
        data-parallax={true}
        ref={pageHeader}
      >
        <div className="filter" />
        <Container>
         
          <div className="motto " >
       
            <h1 style={{fontFamily:'Times New Roman'}}><span className={'title_word_1'} style={{  fontWeight: 'normal' }}>Automated, </span><br />
              <span className={'title_word_2'} style={{  fontWeight: 'normal' }} >Sustainable </span><br />
              <span className={'title_word_3'} style={{  fontWeight: 'normal' }}>Customer-centric </span><br />
              <span className={'title_word_4'} style={{  fontWeight: 'normal' }}>Agri-food </span><br />
              <span className={'title_word_5'} style={{  fontWeight: 'normal' }}>Supply. </span><br /></h1>
            <br />
            <Button
            onClick={()=>{executeScroll()}}
           //   href="#ContactUs"
              className="btn-round mr-1"
              color="neutral"
              target="_blank"
              outline
            >
              <i className="fa fa-play" />
              Try Agricom
            </Button>
            {/* <Button className="btn-round" color="neutral" type="button" outline>
              Download
            </Button> */}
          </div>
        </Container>
      </div>
    </>
  );
}

export default LandingPageHeader;
